.gallery-link {
  flex: 0 100%;
  text-decoration: none;
  display: block;
}

.gallery-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery-item {
  grid-template-rows: 1fr;
  transition: transform .25s;
  display: grid;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery-item:hover {
  transform: scale(1.03);
}

.gallery-info {
  text-align: center;
  justify-content: center;
  align-items: center;
  column-gap: 8px;
  padding: 7px 5px;
  display: flex;
}

p {
  margin: 0;
}

.search-form {
  height: 50px;
  text-align: center;
  background-image: linear-gradient(to bottom right, #3165f5, #cbd6f2);
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-input {
  width: 400px;
  height: 40px;
  border: 1px solid #cbd6f2;
  border-radius: 5px;
  padding-left: 8px;
}

.search-btn {
  height: 40px;
  width: 100px;
  cursor: pointer;
  text-transform: uppercase;
  background-image: linear-gradient(to bottom right, #cbd6f2, #3165f5);
  border: none;
  border-radius: 8px;
  margin-left: 5px;
  padding: 8px 6px 6px;
  font-size: 16px;
  font-weight: 500;
  transition: all .15s;
  box-shadow: 0 0 2px rgba(0, 0, 0, .5);
}

.search-btn:hover {
  transition: all .15s;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
}

.load-more {
  height: 35px;
  width: 150px;
  cursor: pointer;
  text-transform: uppercase;
  background-image: linear-gradient(to bottom right, #cbd6f2, #3165f5);
  border: none;
  border-radius: 8px;
  margin: 20px auto;
  padding: 8px 6px 6px;
  font-weight: 500;
  box-shadow: 0 0 2px rgba(0, 0, 0, .5);
}

.load-more:hover {
  transition: all .15s;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
}

